import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Form from './pages/Form';
import Results from './pages/Results';
import Home from './pages/Home';
import Header from './pages/Header';

function App() {
  return (
    <div>
      <Router>
        <Header />
          <Switch>
            <Route path="/participate">
              <Form />
            </Route>
            <Route path="/results">
              <Results />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
      </Router>
    </div>
    
  );
}

export default App;
