import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import BubbleChartIcon from '@mui/icons-material/BubbleChart';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import MenuItem from '@mui/material/MenuItem';
import validator from 'validator';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import axios from 'axios';
import { Detector } from "react-detect-offline";
import { useHistory, Link } from 'react-router-dom';
import ParticlesBg from 'particles-bg';

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link style={{cursor:"pointer", color: "rgba(0, 0, 0, 0.6)"}} to="/">
        aemigo
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}


const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const theme = createTheme();

export default function Form() {

  const [name, setName] = React.useState('');
  const [email, setEmail] = React.useState('');
  const [input, setInput] = React.useState('');
  const [team, setTeam] = React.useState('Atlantis');
  const [emailError, setEmailError] = React.useState('');
  const [nameError, setNameError] = React.useState('');
  const [inputError, setInputError] = React.useState('');
  const [open, setOpen] = React.useState(false);
  const [bdOpen, setBdOpen] = React.useState(false);
  const [snackbarText, setSnackbarText] = React.useState('');
  const history = useHistory();

  //const v2 = '6LdJi8AcAAAAAEjvD5YNZ7T5_H7oRaayeDNYRzqs'

  const handleTeamChange = (event) => {
    setTeam(event.target.value);
  };
  
  const validateEmail = (e) => {
    var email = e.target.value;
    setEmail(email);
    if (validator.isEmail(email)) {
      if(email.includes("@wipro.com")){
        setEmailError('')
      }
      else
        setEmailError('Only Wipro email IDs are allowed!')
    } else {
      setEmailError('Please enter a valid Wipro email ID!')
    }
  }

  const checkIfEmailExistsAndSubmit = () => {
    setBdOpen(true);
    const req = {
      email: email,
      //name: name,
      team: team,
      input: input
    }
    console.log(req)
    axios.post('/participate',req)
      .then(response => {
        var flag = response.data.exists;
        if(flag){
          setBdOpen(false);
          setEmailError('You have already participated!')
        }else{
          setEmailError("")
          setNameError("")
          setInputError("")
          setEmail('')
          setName('')
          setInput('')          
          setBdOpen(false);
          setSnackbarText('Response is successfully submitted. Thanks for your participation!');
          setOpen(true);
          setTimeout(() => {
            history.push('/results');
          }, 3000);
        }
      })
  }

  // const validateName = (e) => {
  //   setName(e.target.value)
  //   e.target.value.length > 0 ? setNameError('') : setNameError('Please enter your name!')
  // }

  const validateInput = (e) => {
    setInput(e.target.value)
    e.target.value.length > 0 
    ? 
      // (e.target.value.length > 10 
      // ? 
        setInputError('') 
      // : 
      //   setInputError('Your input should be more than 10 characters!')) 
    : 
      setInputError('Please enter your input!')
  }

  const handleSubmit = (event) => {
    event.preventDefault();

    if(email === ''){
      setEmailError("Please enter a valid Wipro email ID!")
    }
    // else if(name === ''){
    //   setNameError("Please enter your name!")
    // }
    else if(input === ''){
      setInputError("Please enter your input!")
    }
    // else if(input.length < 10){
    //   setInputError("Your input should be more than 10 characters!")
    // }
    else if(emailError.length===0 && nameError.length===0 && inputError.length===0){
      //if(event.target[9].value.length > 0)
        checkIfEmailExistsAndSubmit()
      // else{
      //   setSnackbarText('Please check the CAPTCHA to submit your response!');
      //   setOpen(true);
      // }
    }
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setOpen(false);
  };

  // React.useEffect(() => {
  //   const script = document.createElement('script');
  //   script.type = 'text/javascript';
  //   script.async = true;
  //   script.defer = true;
  //   script.src = "https://www.google.com/recaptcha/api.js"
  //   document.getElementById('addScriptHere').appendChild(script);
  // }, [])

  return (
    <Detector
      render={({ online }) => (
        <>
        {/* <h1 style={{color:"white"}}>A E M I G O</h1> */}
        <ThemeProvider theme={theme}>
          <Grid container component="main" sx={{ height: '100vh' }}>
            <CssBaseline />
            <Grid
              item
              xs={false}
              sm={4}
              md={7}
            />
            <ParticlesBg type="thick" bg={true} />
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square mt={10}>
              <Box
                sx={{
                  my: 8,
                  mx: 4,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center'
                }}
              >
                <Avatar sx={{ m: 4, bgcolor: 'secondary.main' }}>
                  <BubbleChartIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                  Participate and unveil your team's logo!
                </Typography>
                <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 4 }}>
                  <TextField
                    error={emailError.length>0}
                    margin="normal"
                    required
                    fullWidth
                    id="email"
                    label="Email Address"
                    name="email"
                    value={email}
                    autoComplete="email"
                    autoFocus
                    helperText={emailError}
                    onChange={validateEmail}
                  />
                  {/* <TextField
                    error={nameError.length>0}
                    margin="normal"
                    required
                    fullWidth
                    id="name"
                    value={name}
                    label="Full Name"
                    name="name"
                    autoComplete="name"
                    onChange={validateName}
                    helperText={nameError}
                  /> */}
                  <RadioGroup row aria-label="team" name="team" defaultValue="Atlantis" onChange={handleTeamChange}>
                    <FormControlLabel value="Atlantis" control={<Radio />} label="Atlantis" />
                    <FormControlLabel value="Olympia" control={<Radio />} label="Olympia" />
                    <FormControlLabel value="Titans" control={<Radio />} label="Titans" />
                    <FormControlLabel value="Wakanda" control={<Radio />} label="Wakanda"/>
                  </RadioGroup>
                  <TextField
                    error={inputError.length>0}
                    multiline
                    rows={3}
                    margin="normal"
                    required
                    fullWidth
                    id="input"
                    value={input}
                    label="Your Thoughts"
                    name="input"
                    autoComplete="input"
                    helperText={inputError}
                    onChange={validateInput}
                  />
                  <p></p>
                  {/* <div className="g-recaptcha" data-sitekey={v2}></div>
                  <div id="addScriptHere" /> */}
                  <Button
                    disabled={!online}
                    type="submit"
                    fullWidth
                    variant="contained"
                    sx={{ mt: 3, mb: 2 }}
                  >
                    Participate!
                  </Button>
                  <Snackbar anchorOrigin={{ vertical:'bottom', horizontal:'right' }} open={online?open:true} autoHideDuration={6000} onClose={handleClose}>
                    <Alert onClose={handleClose} severity={(online && !snackbarText.includes("CAPTCHA"))?"success":"error"} sx={{ width: '100%' }}>
                      {online?snackbarText:"You are offline! Please go online to participate."}
                    </Alert>
                  </Snackbar>
                  <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={bdOpen}
                  >
                    <CircularProgress color="inherit" />
                  </Backdrop>
                  <Copyright sx={{ mt: 4 }} />
                </Box>
              </Box>
            </Grid>
          </Grid>
        </ThemeProvider></>
        )}
     />
  );
}
