import * as React from 'react';
import  {Link, useLocation}  from 'react-router-dom';
import '../styles/home.css';


export default function Header() {

    let location = useLocation();
    const [current, setCurrent] = React.useState("");

    React.useEffect(() => {
        let loc = location.pathname.includes("participate")?"participate":(location.pathname.includes("results")?"results":"home")
        setCurrent(loc);
    }, [location])

    return(
        <>
        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
            Show navigation
          </a>
          <a className="mobile-btn" href="#home" title="Hide navigation">
            Hide navigation
          </a>
          <ul id="nav" className="nav">
            <li className={current === "home" ? "current" : ""}>
              <Link to="/"><strong>Home</strong></Link>
            </li>
            <li className={current === "participate" ? "current" : ""}>
                <Link to="/participate"><strong>Participate</strong></Link>
            </li>
            <li className={current === "results" ? "current" : ""}>
                <Link to="/results"><strong>Results</strong></Link>
            </li>
          </ul>
        </nav>
        </>
    );

}