import * as React from "react";
import { styled } from "@mui/material/styles";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Chip from '@mui/material/Chip';
import Avatar from '@mui/material/Avatar';
import '../styles/results.css';
import axios from 'axios';
import loaderMeta from '../loader-meta.gif';
import ParticlesBg from 'particles-bg';

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
  height: "45vh",
  boxShadow: "1.5px 1.5px 5px grey",
  borderRadius: "10px"
}));

export default function Results() {

  const [imgSrcA, setImgSrcA] = React.useState(loaderMeta);
  const [imgSrcB, setImgSrcB] = React.useState(loaderMeta);
  const [imgSrcC, setImgSrcC] = React.useState(loaderMeta);
  const [imgSrcD, setImgSrcD] = React.useState(loaderMeta);

  function getImages(){
    axios.post('/results','').then(response => {
        const {A, B, C, D} = response.data;
        setImgSrcA(A)
        setImgSrcB(B)
        setImgSrcC(C)
        setImgSrcD(D)
    })
  }

  React.useEffect(() => {
      getImages()
      setInterval(() => {
        getImages()
      }, 5000);
  }, [])

  return (
    
    <Box mt={5} sx={{ width: "98vw", height: "95vh", padding: "1%"}}>
      <ParticlesBg type="lines" bg={true} />
      <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 2 }}>
        <Grid item xs={12} sm={12} md={6}>
          <Item sx={{backgroundColor: "#8f1414"}}>
            <p></p>
            <Chip avatar={<Avatar>A</Avatar>} label="Team Atlantis" color="error"/>
            <p></p>
            <img alt="teamA" className="resultImg" src={imgSrcA} />
          </Item>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Item sx={{backgroundColor: "#004e00"}}>
            <p></p>
            <Chip avatar={<Avatar>O</Avatar>} label="Team Olympia" color="success"/>
            <p></p>
            <img alt="teamB" className="resultImg" src={imgSrcB} />
          </Item>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Item sx={{backgroundColor: "#0000a3"}}>
            <p></p>
            <Chip avatar={<Avatar>T</Avatar>} label="Team Titans" color="primary"/>
            <p></p>
            <img alt="teamC" className="resultImg" src={imgSrcC} />
          </Item>
        </Grid>
        <Grid item xs={12} sm={12} md={6}>
          <Item sx={{backgroundColor: "#b77907"}}>
            <p></p>
            <Chip avatar={<Avatar>W</Avatar>} label="Team Wakanda" color="warning"/>
            <p></p>
            <img alt="teamD" className="resultImg" src={imgSrcD} />
          </Item>
        </Grid>
      </Grid>
    </Box>
  );
}
